import {
	mapState
} from 'vuex';
export default {

	data() {
		return {
			titleNViewBackground: '',
			swiperCurrent: 0,
			swiperLength: 0,
			carouselList: [],
			cateList: [],
			hotList: [{}, {}]
		};
	},

	onLoad() {
		if (this.userInfo == undefined) {
			console.log(this.userInfo)
			uni.navigateTo({
				url: `/pages/public/login`
			})
		}


		this.loadData();
	},
	onBackPress() {

		let that = this;
		if (that.userInfo == undefined) { 
		console.log(that.userInfo)
			if (this.showMask) {
				this.showMask = false;
				return true;
			} else {
				uni.showModal({
					title: '提示',
					content: '是否退出uni-app？',
					success: function(res) {
						if (res.confirm) {
							// 退出当前应用，改方法只在App中生效
							plus.runtime.quit();
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				});
				return true;
			}
		}
	},
	methods: {
		/**
		 * 请求静态数据只是为了代码不那么乱
		 * 分次请求未作整合
		 */
		async loadData() {
			let that = this;
			let carouselList = await this.$api.json('carouselList');

			uni.request({
				url: common.get_goods_listUrl, //仅为示例，并非真实接口地址。
				data: {
					is_mobile: 1,
					category_id: 0,
					page_index: 1,
					page_num: 100000,
					user_id: 0,
					keyword: ''
				},
				method: 'POST',
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: function(res) {
					console.log(res.data.status)
					if (res.data.status == 1) {

						carouselList = res.data.slider;
						let cateList = res.data.category;

						that.titleNViewBackground = carouselList[0].background;
						that.swiperLength = carouselList.length;
						that.carouselList = carouselList;
						that.cateList = cateList;
						let hotList = res.data.data;
						// console.log(hotList)
						that.hotList = hotList;
					} else {



					}



				}
			});




		},
		//轮播图切换修改背景色
		swiperChange(e) {
			const index = e.detail.current;
			this.swiperCurrent = index;
			this.titleNViewBackground = this.carouselList[index].background;
		},
		//详情页
		navToDetailPage() {
			uni.navigateTo({
				url: '/pages/detail/detail'
			})
		}, //详情页
		navToGoodsListPage(sid, tid, title) {
			uni.navigateTo({
				url: `/pages/product/list?fid=${sid}&sid=${sid}&tid=${tid}&title=${title}`
			})

		},
		//商品详情页
		navToGoodsDetailPage(id) {
			let goodsid = id;
			uni.navigateTo({
				url: '/pages/product/product?id=' + goodsid
			})
		}

	},
	 























}
